import {useSelector} from "react-redux";
import Core from "../../services/Core";
import {RawUserInterface} from "./types";
import {useMemo} from "react";
import usersStore from "./store";
import {shallow} from "zustand/shallow";

const allUserQueriesSelector = (state: any): string[] | undefined => state.users.queries.all;


export const useUsers = (): RawUserInterface[] => {
    return usersStore.store(state => {
        const byId = state.byId;
        const allId = state.queries['all'] || []; // user_id
        return allId.map(id => {
            return byId[id]
        }).filter(Boolean)
    }, shallow)
}

export const useUser = (id: string): RawUserInterface | undefined => {
    return usersStore.store(state => (state.byId || {})[id], shallow)
}

export const useUserByUsername = (username: string): RawUserInterface | undefined => {
    return usersStore.store(state => (state.byId || {})[(state.byUsername || {})[username]], shallow)
};

export const useMeSelector = (state: any): RawUserInterface => state.users.byId[Core.currentUserId];

export const useMe = (): RawUserInterface | undefined => {
    return usersStore.store(state => state.byId[Core.currentUserId], shallow)
}

export const getMe = (): RawUserInterface | undefined => {
    return (usersStore.store.getState().byId || {})[Core.currentUserId]
}

export const useUsersByUsernames = (usernames: string[] = []) => {
    return usersStore.store(state => usernames.map(username => (state.byId || {})[(state.byUsername || {})[username]]).filter(Boolean), shallow)
};

export const useUsersByIds = (ids: string[]) => {
    return usersStore.store(state => ids.map(id => (state.byId || {})[id]).filter(Boolean), shallow)
};


type Options = {
    userIds?: string[],
    includingMe?: boolean,
}

export const useUsersByKeyword = (keyword: string, options?: Options) => {
    const storeUserIds = (usersStore.store.getState().queries || {})['all'];
    const byId = usersStore.store.getState().byId || {}
    const me = useMe();

    return useMemo(() => {
        const newOptions = {
            userIds: undefined,
            includingMe: true,
            ...options
        };
        let result: RawUserInterface[] = [];
        const userIds = newOptions.userIds || storeUserIds; // if not set, then get all users

        if (!keyword) {
            for (let i = 0 ; i< userIds.length; i++) {
                const id = userIds[i];
                if (byId[id]){
                    result.push(byId[id])
                }
            }
            return result
        }

        for (let i = 0 ; i< userIds.length; i++) {
            const id = userIds[i];
            const user = byId[id];

            if (user && user?.keyword.includes(keyword)){
                result.push(byId[id])
            }
        }

        return result
    }, [keyword, options]);
};

export const useAllUserIds = (): string[] => {
    return useSelector(allUserQueriesSelector) || [];
}
