import {useState, useEffect, useMemo} from 'react';

type setDataType<DataType> = (data: DataType) => any;
type idType = string | number

const eventListeners: Record<string, Record<idType, setDataType<any>>> = {};
const addEventListener = <DataType>(name: string | number, id: string | number, setData: setDataType<DataType>) => {
    const listeners = eventListeners[name] || {};
    return Object
        .assign(
            eventListeners,
            {
                [name]: {
                    ...listeners,
                    [id]: setData,
                },
            },
        );
};

const removeEventListener = (name: string | number, id: string) => (
    delete eventListeners[name][id]
);


export const useListener = <DataType>(name: string | number, callback: (data: DataType) => any) => {
    const id = useMemo(() => Math.random().toString(32), []);

    useEffect(() => {
        addEventListener(name, id, callback);
        return () => {
            removeEventListener(name, id);
        }
    }, [callback, name, id]);
};

export const useEvent = <DataType>(name: string | number, withInitialData: DataType) => {
    const [data, setData] = useState<DataType>(withInitialData);
    useListener(name, setData);
    return data;
};

export const emitEvent = (name: string | number, withData: any) => {
    const listeners = eventListeners[name] || {};
    Object.values(listeners).map(setData => setData(withData));
};
