import React, {createContext, ReactElement, useContext, useEffect, useMemo, useState} from "react";

import en from './languages/en';
import vi from './languages/vi';
import {LanguageEnum} from "../types";
import {getCurrentLanguage, useLanguage} from "../store/global/hooks";

// ** ** ** ** ** CREATE ** ** ** ** **
const BaseDictionaryContext = createContext<typeof en>(en);

// ** ** ** ** ** USE ** ** ** ** **
export function useBaseDictionary() {
  const context = useContext(BaseDictionaryContext);
  if (context === undefined || context === null) {
    throw new Error(
      '`useBaseDictionary` hook must be used within a `BaseDictionaryProvider` component',
    );
  }
  // const dictionary = useBaseDictionary()
  return context as typeof en;
}

// ** ** ** ** ** GET ** ** ** ** **
export function getBaseDictionary() {
  const language = getCurrentLanguage();
  const languageDictionaryMap = {
    [LanguageEnum.EN]: en,
    [LanguageEnum.VI]: vi
  };
  return languageDictionaryMap[language]
}

// ** ** ** ** ** PROVIDE ** ** ** ** **
export function BaseDictionaryProvider({children}: {children: ReactElement}) {
  const currentLanguage = useLanguage()

  const languageDictionaryMap = useMemo(() => {
    return {
      [LanguageEnum.EN]: en,
      [LanguageEnum.VI]: vi
    }
  }, [])

  const [dictionary, setDictionary] = useState<typeof en>(languageDictionaryMap[currentLanguage]);

  useEffect(() => {
    setDictionary(languageDictionaryMap[currentLanguage]);
  }, [currentLanguage]);

  // ** ** ** ** ** RENDER ** ** ** ** **
  return (
    <BaseDictionaryContext.Provider value={dictionary}>
      {children}
    </BaseDictionaryContext.Provider>
  );
}
