import { DEFAULT_SHARE_URL, DEFAULT_SOCKET_URL, DEFAULT_SYS_URL } from '../constants';


class Core {
    clientKey = '';
    clientSecret = '';
    currentUserId = '';
    systemId = '';
    shareUrl = DEFAULT_SHARE_URL;
    socketUrl = DEFAULT_SOCKET_URL;
    sysUrl = DEFAULT_SYS_URL || 'base.vn';
    systemToken = '';
    systemName = '';
    intercomToken = '';

    private _accessToken = '';

    get accessToken() {
        return this._accessToken;
    }

    set accessToken (accessToken: string) {
        const parts = accessToken.match(/^(\d+)\.(\d+)\./);
        // @ts-ignore
        this.currentUserId = parts ? parts[1] : "";
        // @ts-ignore
        this.systemId = parts ? parts[2] : "";

        this._accessToken = accessToken;
    }
}

export default new Core();
