import {create, SetState, GetState, StateCreator} from 'zustand'
import {persist, PersistOptions} from 'zustand/middleware'
import AsyncStorage from '@react-native-async-storage/async-storage';
import {RawUserInterface, UsersState} from "./types";
import {normalizeStringForSearch} from "../../utils/utils";

type usersStorePersists = (
    config: StateCreator<UsersState>,
    options: PersistOptions<UsersState>
) => StateCreator<UsersState>

const usersStore = create<UsersState>(
    (persist as usersStorePersists)(
        (set, get) => ({
            byId: {},
            byUsername: {},
            queries: {}
        }),
        {
            name: 'usersStore',
            getStorage: () => AsyncStorage
        }
    )
)

const setUsersAction = (users: RawUserInterface[]) => {
    const newState: UsersState = {
        byId: {},
        byUsername: {},
        queries: {},
    };
    users.forEach(user => {
        const keyword = `${user.name}${user.username}${user.title}`;
        newState.byId[user.id] = {
            ...user,
            keyword: `${keyword}${normalizeStringForSearch(keyword)}`
        };
        newState.byUsername[user.username] = user.id;
    });

    usersStore.setState(state => ({
        ...state,
        byId: {
            ...state.byId,
            ...newState.byId
        },
        byUsername: {
            ...state.byUsername,
            ...newState.byUsername
        }
    }));
    return
}
const syncUsersAction = (users: RawUserInterface[]) => {
    const newState = {...usersStore.getState()};

    users.forEach(user => {
        const keyword = `${user.name}${user.username}${user.title}`;
        newState.byId[user.id] = {
            ...user,
            keyword: `${keyword}${normalizeStringForSearch(keyword)}`
        };
        newState.byUsername[user.username] = user.id;
    });

    usersStore.setState(state => ({
        ...state,
        byId: {
            ...state.byId,
            ...newState.byId
        },
        byUsername: {
            ...state.byUsername,
            ...newState.byUsername
        }
    }));
    return
};
const setUsersQueries = (payload: Record<string, string[]>) => {
    usersStore.setState(state => ({
        ...state,
        queries: {
            ...payload
        }
    }))
    return
}
const resetUsers = () => {
    usersStore.setState({
        byId: {},
        byUsername: {},
        queries: {}
    })
}

export default {
    store: usersStore,
    syncUsersAction,
    setUsersAction,
    setUsersQueries,
    resetUsers,
}
