import {create, StateCreator} from 'zustand'
import {persist, PersistOptions} from 'zustand/middleware'
import AsyncStorage from '@react-native-async-storage/async-storage';
import {GlobalState} from "./types";
import {ThemeNameEnum} from "../../types";

type globalStorePersists = (
    config: StateCreator<GlobalState>,
    options: PersistOptions<GlobalState>
) => StateCreator<GlobalState>

const globalStore = create<GlobalState>(
    (persist as globalStorePersists)(
        (set, get) => ({
            theme: ThemeNameEnum.WHITE
        }),
        {
            name: 'globalStore',
            getStorage: () => AsyncStorage
        }
    )
)

const setThemeAction = (theme: ThemeNameEnum) => {
    globalStore.setState({theme: theme})
}

const getCurrentTheme = () => {
    return globalStore.getState().theme
}

const setSettingAction = (keyName: string, value: string) => {
    globalStore.setState({[keyName]: value})
}

const getSettingAction = (keyName: string) => {
    return (globalStore.getState() || {})[keyName] || "";
}
export default {
    store: globalStore,
    setThemeAction,
    getCurrentTheme,
    setSettingAction,
    getSettingAction
}
